@import "utilities/variables";
@import "utilities/mixins";
@import "components/buttons";

.future-events-list {
  li {
    margin: map-get($spacing, base) 0;
    padding: map-get($spacing, sm);
    border-bottom: 1px solid map-get($colors, border);
  }
}

.status-text {
  display: inline-block;
  margin-left: 1rem;
  padding: 0.3rem 0.8rem;
  border-radius: 4px;
  font-weight: 600;
  transition: opacity 0.3s ease;

  &.accepted-text {
    color: map-get($colors, success);
    background: lighten(map-get($colors, success), 40%);
    border: 1px solid lighten(map-get($colors, success), 30%);
  }

  &.declined-text {
    color: map-get($colors, danger);
    background: lighten(map-get($colors, danger), 40%);
    border: 1px solid lighten(map-get($colors, danger), 30%);
  }
}
